.storeContent{
    width:1400px; 
    // border:1px solid red;
   .line1{
       margin-top:50px;
       height:300px;
       text-align: left; 
       vertical-align: center;
       position: relative;
       border:1px solid #eee;
       box-shadow:1px 2px 2px #eee;
       .leftImg{
           display: inline-block;
           height: 300px;
          img{
              width:700px;
              height: 100%;
              z-index: -1;
              
          }
       }
       .rightText{
            // border:1px solid red;
            // margin:700px;
            display: inline-block;
            height: 300px;
            position: absolute;
            left: 800px;
            p{
               margin-top:30px;
               font-size: 25px;
            //    color:#000;
            }
            h3{

            }
       }
   }
   .line2{
       margin-top:20px;
       display:flex;
       justify-content: space-around;
       .box{
           flex:0 0 32%;
           .TopImg{
               img{
                   width:100%;
               }
           }
           .BotText{
               border:1px solid #eee;
               border-top: none;
               p{
                   margin-top:10px;
               }
           }
       }
   }
}
